'use client';

import { useState, useEffect, useRef } from 'react';
import { videoApi } from '@/lib/api/video-api';
import type { VideoSource } from '@/lib/api/video-api';
import { useRouter } from 'next/navigation';
import { X, Settings, Play, RotateCcw } from 'lucide-react';

interface VideoPlayerProps {
  mediaId: number;
  mediaType: 'movie' | 'tv';
  title?: string;
  season?: number;
  episode?: number;
  onClose?: () => void;
}

export default function VideoPlayer({
  mediaId,
  mediaType,
  title,
  season,
  episode,
  onClose
}: VideoPlayerProps) {
  const [embedUrl, setEmbedUrl] = useState<string>('');
  const [sources, setSources] = useState<VideoSource[]>([]);
  const [currentSourceIndex, setCurrentSourceIndex] = useState(0);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [showSources, setShowSources] = useState(false);

  const iframeRef = useRef<HTMLIFrameElement>(null);
  const router = useRouter();

  // Load video sources
  useEffect(() => {
    let cancelled = false;

    const loadVideo = async () => {
      setLoading(true);
      setError(null);

      try {
        let videoSources: VideoSource[] = [];

        if (mediaType === 'movie') {
          videoSources = await videoApi.getMovieSources(mediaId);
        } else {
          videoSources = await videoApi.getTVSources(mediaId, season || 1, episode || 1);
        }

        if (cancelled) return;

        if (!videoSources.length) {
          setError('No video sources available');
          return;
        }

        setSources(videoSources);
        setEmbedUrl(videoSources[0].url);
        setCurrentSourceIndex(0);
      } catch {
        if (!cancelled) setError('Failed to load video');
      } finally {
        if (!cancelled) setLoading(false);
      }
    };

    loadVideo();
    return () => { cancelled = true };
  }, [mediaId, mediaType, season, episode]);

  // Handle iframe errors
  const handleVideoError = () => {
    if (currentSourceIndex < sources.length - 1) {
      const nextIndex = currentSourceIndex + 1;
      setCurrentSourceIndex(nextIndex);
      setEmbedUrl(sources[nextIndex].url);
    } else {
      setError('All video sources failed to load');
    }
  };

  const handleClose = () => {
    if (onClose) onClose();
    else router.back();
  };

  const switchSource = (i: number) => {
    setCurrentSourceIndex(i);
    setEmbedUrl(sources[i].url);
    setShowSources(false);
    setError(null);
  };

  const retryCurrent = () => {
    if (iframeRef.current) iframeRef.current.src = iframeRef.current.src;
  };

  // Escape closes
  useEffect(() => {
    const handleKeyDown = (e: KeyboardEvent) => {
      if (e.key === 'Escape') handleClose();
    };
    document.addEventListener('keydown', handleKeyDown);
    return () => document.removeEventListener('keydown', handleKeyDown);
  }, []);

  const mediaTitle = title || (mediaType === 'movie' ? 'Movie' : 'TV Show');

  return (
    <div className="fixed inset-0 z-50 bg-black">
      {/* Top bar */}
      <div className="absolute top-4 left-4 right-4 z-20 flex items-center justify-between">
        <div>
          <h1 className="text-white text-xl font-bold">{mediaTitle}</h1>
          {mediaType === 'tv' && season && episode && (
            <p className="text-gray-400 text-sm">Season {season} • Episode {episode}</p>
          )}
        </div>
        <div className="flex items-center gap-3">
          {sources.length > 1 && (
            <button
              onClick={() => setShowSources(v => !v)}
              className="bg-black/50 hover:bg-black/70 text-white p-2 rounded-full"
            >
              <Settings className="w-5 h-5" />
            </button>
          )}
          <button
            onClick={handleClose}
            className="bg-black/50 hover:bg-black/70 text-white p-2 rounded-full"
          >
            <X className="w-5 h-5" />
          </button>
        </div>
      </div>

      {/* Source menu */}
      {showSources && sources.length > 1 && (
        <div className="absolute top-16 right-4 bg-black/90 backdrop-blur-sm rounded-lg p-4 z-30 w-60">
          <h3 className="text-white text-sm font-semibold mb-3">Servers</h3>
          <div className="space-y-2">
            {sources.map((s, i) => (
              <button
                key={i}
                onClick={() => switchSource(i)}
                className={`w-full text-left px-3 py-2 rounded text-sm transition-colors ${
                  i === currentSourceIndex
                    ? 'bg-blue-600 text-white'
                    : 'bg-gray-800 text-gray-300 hover:bg-gray-700'
                }`}
              >
                {s.servers} ({s.quality})
              </button>
            ))}
          </div>
        </div>
      )}

      {/* Loading */}
      {loading && (
        <div className="absolute inset-0 flex items-center justify-center text-white">
          <div className="text-center">
            <div className="animate-spin rounded-full h-12 w-12 border-2 border-white border-t-transparent mb-4 mx-auto"></div>
            <p>Loading video...</p>
          </div>
        </div>
      )}

      {/* Error */}
      {error && !loading && (
        <div className="absolute inset-0 flex items-center justify-center text-white">
          <div className="text-center max-w-md">
            <div className="text-6xl mb-4">⚠️</div>
            <h2 className="text-xl font-bold mb-2">Video Unavailable</h2>
            <p className="text-gray-400 mb-4">{error}</p>
            <div className="flex gap-3 justify-center">
              {currentSourceIndex < sources.length - 1 && (
                <button
                  onClick={() => switchSource(currentSourceIndex + 1)}
                  className="px-4 py-2 bg-blue-600 hover:bg-blue-700 rounded-lg flex items-center gap-2"
                >
                  <Play className="w-4 h-4" /> Next Server
                </button>
              )}
              <button
                onClick={retryCurrent}
                className="px-4 py-2 bg-gray-600 hover:bg-gray-700 rounded-lg flex items-center gap-2"
              >
                <RotateCcw className="w-4 h-4" /> Retry
              </button>
            </div>
          </div>
        </div>
      )}

      {/* Video */}
      {embedUrl && !loading && !error && (
        <iframe
          ref={iframeRef}
          src={embedUrl}
          className="w-full h-full"
          allowFullScreen
          allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
          onError={handleVideoError}
          style={{ border: 'none' }}
        />
      )}
    </div>
  );
}
